@import './_config.scss';

article{
	@include fullScreen;
	height: 100%;
	background-color: rgba(255,255,255,0.95);
	backdrop-filter: blur(5px);
	overflow-y: auto;
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;

	#close{
		position: fixed;
		top: 0;
		right: 0;
		padding: 12px 16px;
		font-size: 3rem;
		line-height: 1;
		opacity: 0.7;
		color: $blackText;

		@include mediaTablet {
			font-size: 5rem;
		}

		@include mediaDesktop {
			font-size: 64px;
			padding: 32px 48px 16px 16px;
			&:hover{
				color: black;
			}
		}
	}
}

#content{
	// overflow-y: scroll;
	height: 100vh;
	padding: 4rem 12vw 6rem;

	@include mediaDesktop {
		padding: $whitespace 20vw;
	}

	h1{
		line-height: 1;
		font-family: $oswald;
		font-size: 2.5rem;
		font-weight: 400;
		margin-bottom: .66rem;

		@include mediaTablet {
			font-size: 2.8rem;
		}

		@include mediaDesktop {
			font-size: 2.5rem;
			margin-bottom: 8px;
		}
	}

	ul#tech{
		color: $greyText;
		font-size: .9rem;
		text-transform: uppercase;
		font-weight: 600;
		display: flex;
		flex-wrap: wrap;
		@include mobileMargin('bottom');

		@include mediaDesktop {
			@include desktopMargin('bottom');
		}

		li{
			margin-right: 12px;
		}
	}

	#info{
		@include mobileMargin('bottom');
		@include mediaDesktop {
			@include desktopMargin('bottom');
		}
		p{
			@include roboto(300);
			font-size: 1.33rem;
			line-height: 1.5;
			margin-bottom: .33rem;

			@include mediaTablet {
				font-size: 1.5rem;
			}

			@include mediaDesktop {
				font-size: 20px;
			}
		}
	}

}