@import url("https://fonts.googleapis.com/css?family=Oswald|Rancho|Roboto:300,400,500");
* {
  padding: 0;
  margin: 0;
  font-family: inherit;
  list-style: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
  *:before, *:after {
    box-sizing: inherit; }
  *::selection {
    background-color: #673AB7;
    color: white; }

html {
  box-sizing: border-box; }
  html body {
    font-family: sans-serif;
    color: rgba(0, 0, 0, 0.8);
    font-family: "Roboto", "Helvetica", "Helvetica Neue", "San Francisco", "Arial", "Verdana", sans-serif;
    font-weight: 400; }

a {
  text-decoration: none;
  color: inherit; }
  a:hover {
    text-decoration: none; }

button:hover {
  cursor: pointer; }

.btn {
  color: white;
  background-color: #673AB7;
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 2.5;
  border-radius: 2px;
  text-align: center;
  box-shadow: 2px 2px 0 rgba(0, 0, 0, 0.2);
  user-select: none; }
  @media (min-width: 768px) and (orientation: portrait) {
    .btn {
      font-size: 1.33rem; } }
  @media (min-width: 1024px) and (orientation: landscape) {
    .btn {
      font-size: 1.33rem; } }
  @media (min-width: 1280px) and (orientation: landscape) {
    .btn {
      font-size: 18px; }
      .btn:hover {
        opacity: .8; } }

@media (min-width: 1280px) and (orientation: landscape) {
  header, main {
    max-width: 50vw;
    width: 600px; } }

.articleAnimation-enter-active,
.articleAnimation-leave-active {
  transition: opacity 250ms ease-out, transform 200ms ease-out; }

.articleAnimation-enter,
.articleAnimation-leave-to {
  transform: translateY(2vh);
  opacity: 0; }

.loadingAnimation-leave-active {
  transition: opacity 1000ms; }

.loadingAnimation-leave-to {
  opacity: 0; }

[v-cloak] {
  position: hidden; }
